@-webkit-keyframes logoanimation{
    0%{
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
    100%{
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
}

@keyframes logoanimation{
    0%{
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
    100%{
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
}

@-webkit-keyframes infinite-row-one{
    0%{
        -webkit-transform: translate3d(0px, 0, 0);
                transform: translate3d(0px, 0, 0);
    }
    100%{
        -webkit-transform: translate3d(-104.57%, 0 , 0);
                transform: translate3d(-104.57%, 0 , 0);
    }
}

@keyframes infinite-row-one{
    0%{
        -webkit-transform: translate3d(0px, 0, 0);
                transform: translate3d(0px, 0, 0);
    }
    100%{
        -webkit-transform: translate3d(-104.57%, 0 , 0);
                transform: translate3d(-104.57%, 0 , 0);
    }
}

@-webkit-keyframes infinite-row-two{
    0%{
        -webkit-transform: translate3d(-9%, 0, 0);
                transform: translate3d(-9%, 0, 0);
    }
    100%{
        -webkit-transform: translate3d(-113.57%, 0 , 0);
                transform: translate3d(-113.57%, 0 , 0);
    }
}

@keyframes infinite-row-two{
    0%{
        -webkit-transform: translate3d(-9%, 0, 0);
                transform: translate3d(-9%, 0, 0);
    }
    100%{
        -webkit-transform: translate3d(-113.57%, 0 , 0);
                transform: translate3d(-113.57%, 0 , 0);
    }
}
@media(max-width:1100px){
    .side-bar{
        position: inherit;
        height: auto;
        width: 100%;
        background-color: white;
        padding-bottom: 10px;
    }
    .side-bar-logo{
        display: none;
    }
    .side-bar-links{
        display: -webkit-flex;
        display: flex;
    }
    .side-bar-link{
        display: block;
        text-align: center;
        color: #c11a2b;
        font-weight: bold;
        padding: 0;
        position: relative;
        font-size: 18px;
    }
    .side-bar-link:hover{
        background-color: transparent;
    }
    .side-bar-link.active::before{
        content: '';
        position: absolute;
        bottom:-10px;
        right: 40px;
        left: 40px;
        height: 2px;
        background-color: #c11a2b;
    }
    .card{
        width: 230px;
        height: 330px;
    }
    .card-top{
        width: 230px;
        height: 330px;
    }
    .card-bottom{
        max-width: 230px;
    }
    .welcome-title{
