        line-height: 20px;
    }
    .welcome-inner{
        display: block;
    }
    .welcome-right{
        width: 100%;
    }
    .infinite-poster{
        width: 250px;
        height: 9.5rem;
        border-radius: .8rem;
        margin-right: 16px;
    }
    .infinite-posters{
        width: calc(250px * 40);
    }
    @-webkit-keyframes infinite-row-one{
        0%{
            -webkit-transform: translate3d(0px, 0, 0);
                    transform: translate3d(0px, 0, 0);
        }
        100%{
            -webkit-transform: translate3d(-106.37%, 0 , 0);
                    transform: translate3d(-106.37%, 0 , 0);
        }
    }
    @keyframes infinite-row-one{
        0%{
            -webkit-transform: translate3d(0px, 0, 0);
                    transform: translate3d(0px, 0, 0);
        }
        100%{
            -webkit-transform: translate3d(-106.37%, 0 , 0);
                    transform: translate3d(-106.37%, 0 , 0);
        }
    }
    
    @-webkit-keyframes infinite-row-two{
        0%{
            -webkit-transform: translate3d(-9%, 0, 0);
                    transform: translate3d(-9%, 0, 0);
        }
        100%{
            -webkit-transform: translate3d(-115.37%, 0 , 0);
                    transform: translate3d(-115.37%, 0 , 0);
        }
    }
    
    @keyframes infinite-row-two{
        0%{
            -webkit-transform: translate3d(-9%, 0, 0);
                    transform: translate3d(-9%, 0, 0);
        }
        100%{
            -webkit-transform: translate3d(-115.37%, 0 , 0);
                    transform: translate3d(-115.37%, 0 , 0);
        }
    }
    .welcome-left{
        width: 100%;
    }
    .movie-section{
        display: block;
    }
    .movie-right{
        width: 100%;
    }
    .movie-poster{
        width: 300px !important;
    }
    main svg{
        height: 25px;
    }
    iframe{
        height: 200px;
    }
    .movie-backdrop{
        top: 20px;
    }
    .movie-left{
        display: -webkit-flex;
        display: flex;
        width: 100%;
    }
    .side-bar-link{
        font-size: 14px;
    }
    .person-card{
        display: block;
    }
    .person-card-top{
        width: 100%;
    }
    .person-card-bottom{
        width: 100%;
    }
    .person-details{
        display: block;
    }
    .person-details-column-one,
    .person-details-column-two{
        display: block;
        width: 100%;
    }
    .footer-logo{
        width: 60px;
        height: 60px;
    }
    .movie-cast{
        margin-top: 20px;
    }
    .movie-vote-average{
        padding: 0 15px;
    }
    .movie-backdrop{
        top: 100px;
        height: 260px;
    }
    .movie-left{
        -webkit-justify-content: center;
                justify-content: center;
    }
    .movie-detail{
        width: 100%;
    }
    .searched-movies-list .card{
        width: 33%;
        margin-top: 0;
        height: auto;
    }
