}

@media(max-width:440px){
    .card{
        width: 110px;
        height: 170px;
    }
    .card-top{
        width: 110px;
        height: 170px;
    }
    .card-title{
        font-size: 12px;
    }
    .card-bottom{
        max-width: 110px;
    }
    .side-bar-link.active::before{
        left: 5px;
        right: 5px;
    }
    .searched-movies-list .card{
        width: 33% ;
    }
    .footer-copyrights{
        display: block;
    }
    .footer-copyright-left{
        width: 100%;
    }
    .footer-copyright-right{
        width: 100%;
    }
}

